import { useState, useEffect } from 'react';
import Preloader from './components/Preloader';
import Banner from './components/Banner';
import Navbar from './components/Navbar';
import './App.css';
import Footer from './components/Footer';
import Modal from './components/Modal';

function App() {
  const [loading, setLoading] = useState(true);

  const asyncCall = ():any => {
    return new Promise<void>((resolve) => setTimeout(() => resolve(), 1000));
  }

  useEffect(() => asyncCall().then(() => {
    setLoading(false);
  }), []);

  if(loading) {
    return <Preloader size={70} borderSize={5} />;
  }

  return (
    <div className="mx-auto">
      <Banner />
      <Navbar />
      <div className="mx-auto container-fluid py-5 text-center bg-whitesmoke row" id="section-1">
        <h1 className="title pt-5">Portfolio</h1>
        <hr />
        <div className="container-fluid my-5 row">
          <Modal />
        </div>
      </div>
      <div id="section-2" className="py-5 text-center">
        <br/><br/>
        <Footer />
      </div>
    </div>
  );
}

export default App;
