import logo from "./images/icon.png";

const Navbar = () => {

    const NavbarStyles: any = {
        boxShadow: '0px 8px 8px -6px rgba(0,0,0,.5)'
    }

    return (
        <nav className="navbar fixed navbar-fixed-top sticky-top bg-nav"
            style={NavbarStyles}>
            <div className="mx-auto container-fluid row mt-2">
                <div className="col-md-6 row text-info">
                    <div>
                        <span className="circle font-weight-bold h4 font">
                            <img className="rounded-circle" src={logo} alt={logo} width="25px" height="25px" />&nbsp;JG&nbsp;
                        </span>
                        <small className="font-weight-bold mt-2">Developer</small>
                    </div>

                </div>
                <div className="col-md-3 d-flex justify-content-around">
                    <a id="btnSection1" href="#section-1" className="h3 btn btn-outline-info btn-lg">Portfolio</a>
                    <a id="btnSection2" href="#section-2" className="h4 btn btn-outline-info btn-lg">Stacks</a>
                </div>
            </div>
        </nav>
    );
}

export default Navbar;