import { Fragment, useEffect, useState } from 'react';

const Banner = () => {
    const [scroll, setScroll] = useState(0)
    const BannerStyles: any = {
        boxShadow: '0px 8px 8px -6px rgba(0,0,0,.5)'
    }


    useEffect(() => {
        const onScroll = (e: any) => {
            setScroll(e.target.documentElement.scrollTop);
        };
        window.addEventListener("scroll", onScroll);

        return () => window.removeEventListener("scroll", onScroll);
    }, [scroll]);

    return (
        <Fragment>
            <div className="d-flex justify-content-end align-items-end">
                {window && <a href="#home" className={window.pageYOffset > 50 ? "text-white onScroll show" : "text-white onScroll"}><i className="fas fa-angle-up rounded-circle homeBtn"
                    style={BannerStyles}></i></a>}
            </div>
            <div id="home" className="navbar header">
                <div className="top-right"><p>Updated: Sept 2021</p></div>
                <div id="head" className="mx-auto container text-white font-big d-flex justify-content-center">
                    <div className="container text-center">
                        <p>Jacsel Guridi Diaz</p>
                        <hr />
                        <p className="h5 text-light title">Software Developer</p>
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default Banner;